#header {
  background-image: -webkit-gradient(linear, top, bottom, color-stop(0, #00FFFF), color-stop(1, #005252));
  background-image: -o-linear-gradient(top, #00FFFF, #005252);
  background-image: -moz-linear-gradient(top, #00FFFF, #005252);
  background-image: -webkit-linear-gradient(top, #00FFFF, #005252);
  background-image: linear-gradient(to bottom, #00FFFF, #005252);
  padding: 40px 0;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
  justify-content: space-between;
}

.header__logo {
  background-color: #00FFFF;
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(25% - 32px);
}

.header__nav {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(75% - 32px);
}

/* Add a black background color to the top navigation */

.topnav {
  background-color: #333;
  overflow: hidden;
}

/* Style the links inside the navigation bar */

.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Add an active class to highlight the current page */

.active {
  background-color: #4CAF50;
  color: white;
}

/* Hide the link that should open and close the topnav on small screens */

.topnav .icon {
  display: none;
}

/* When the screen is less than 600 pixels wide, hide all links, except for the first one ("Home"). Show the link that contains should open and close the topnav (.icon) */

