#about {
  padding: 50px 0 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

.about {
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
}

.about__col {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(25% - 32px);
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 15px 5px #008080;
}

.about__header {
  padding: 30px 10px;
  margin-bottom: 30px;
  background-color: #8080FF;
  border-radius: 10px 10px 0 0;
  text-align: center;
  color: #00FFFF;
  font-weight: bold;
  text-transform: uppercase;
}

.about__content {
  padding: 0 20px;
  text-align: justify;
}

.about__content p {
  color: #3B3B3B;
  margin-bottom: 10px;
}

.about__footer {
  padding: 30px 10px;
  margin-bottom: 30px;
  background-color: #8080FF;
  border-radius: 0 0 10px 10px;
  text-align: center;
  color: #00FFFF;
  font-weight: bold;
  text-transform: uppercase;
}

#services {
  padding: 50px 0 20px;
}

.services {
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
}

.services__col {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 32px);
  display: flex;
  flex-direction: column;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1280px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.block {
  padding: 30px 0;
  margin-bottom: 5px;
  box-sizing: border-box;
  border: 1px solid #2c3e50;
}

