@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .header__logo {
    width: calc(33.333333333333336% - 32px);
  }

  .header__nav {
    width: calc(66.66666666666667% - 32px);
  }

  .about__col {
    width: calc(50% - 32px);
  }

  .about__col {
    margin-bottom: 30px;
  }

  .services {
    justify-content: center;
    margin-bottom: 10px;
  }

  .services__col {
    width: calc(41.66666666666667% - 32px);
  }

  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 720px) {
  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav a.icon {
    float: right;
    display: block;
  }

  .topnav.responsive {
    position: relative;
  }

  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }

  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }

  .services {
    justify-content: center;
    margin-bottom: 10px;
  }

  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .header__logo {
    width: calc(100% - 32px);
  }

  .header__nav {
    width: calc(100% - 32px);
  }

  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav a.icon {
    float: right;
    display: block;
  }

  .topnav.responsive {
    position: relative;
  }

  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }

  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }

  .about {
    justify-content: center;
  }

  .about__col {
    width: calc(83.33333333333334% - 32px);
  }

  .about__col {
    margin-bottom: 10px;
  }

  .services {
    justify-content: center;
    margin-bottom: 10px;
  }

  .services__col {
    width: calc(83.33333333333334% - 32px);
  }

  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}